<template>
  <div class="d-flex row w-100">
    <div
      v-if="botsGenerated === false"
      class="col-lg-4 col-xl-3 d-flex flex-column flex-shrink-0 p-2 bg-light"
    >
      <div class="d-flex justify-content-center align-items-center mb-3">
        <h1>WELCOME</h1>
      </div>
      <hr />
      <p class="p-4 fs-4">
        Hello and welcome, you can change the default goal by clicking on the
        map, then you have to generate the cars and you can start the race. you
        can also add cars before or after starting the race.
      </p>
    </div>

    <div
      v-else
      class="col-lg-4 col-xl-3 d-flex flex-column flex-shrink-0 p-2 bg-light"
    >
      <div class="d-flex justify-content-center align-items-center mb-3">
        <h1>POSITIONS</h1>
      </div>
      <hr />

      <div class="my-3 overflow-auto" style="height: 50vh">
        <table class="table table-bordered table-striped">
          <thead>
            <tr class="table-dark">
              <td>Pos</td>
              <td>Name</td>
              <td>Distance</td>
              <td>Battery</td>
            </tr>
          </thead>
          <tbody>
            <tr
              class="align-middle"
              v-for="(car, index) in bots"
              :key="index"
              @click="infoCar(car)"
            >
              <th>{{ index + 1 }}</th>
              <td>{{ car.title }}</td>
              <td>{{ Math.floor(car.distance) + ' mts' }}</td>

              <td>
                <div
                  v-if="car.battery > 0"
                  :style="{
                    fontWeight: 'bold',
                    borderRadius: '3px',
                    width: '100%',
                    textAlign: 'center',
                    color: '#E5B301',
                    backgroundImage: `linear-gradient(90deg, rgb(17,59,8) ${car.battery}%, rgb(142,22,0) ${car.battery}% )`,
                  }"
                >
                  {{ car.battery }}
                </div>
                <span
                  v-else
                  style="
                    text-align: center;
                    font-size: 0.8rem;
                    font-weight: bold;
                    color: rgb(142, 22, 0);
                  "
                  >Wait {{ " " + car.batteryRechargeTime + "s" }}</span
                >
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-if="finishedRace && raceStarted" class="text-center">
        <h4>
          The race is over, congratulations to
          <span class="badge bg-info">{{ " " + bots[0].title }}</span>
        </h4>
      </div>
    </div>

    <div class="col d-flex flex-column align-items-center p-2">
      <div class="mb-3 mb-md-0">
        <h1>BOT RACE</h1>
      </div>

      <div class="container d-flex row my-2 justify-content-around text-center">
        <div class="col-xs-6 col-sm-2 my-1">
          <button
            v-if="!raceStarted"
            class="btn btn-primary"
            @click="generateBots"
          >
            Generate Cars
          </button>
          <button v-else class="btn btn-primary" @click="generateBots">
            Generate Again
          </button>
        </div>
        <div class="col-xs-6 col-sm-2 my-1">
          <button
            class="btn btn-primary"
            :disabled="finishedRace"
            @click="addCar"
          >
            Add Car
          </button>
        </div>
        <div v-if="botsGenerated" class="col-xs-6 col-sm-2 my-1">
          <button
            class="btn btn-success"
            :disabled="raceStarted"
            @click="moveBots"
          >
            Start Race
          </button>
        </div>
        <div v-if="botsGenerated" class="col-xs-6 col-sm-2 my-1">
          <button
            class="btn btn-danger"
            :disabled="finishedRace"
            @click="stopRace"
          >
            Stop Race
          </button>
        </div>
      </div>

      <div style="width: 100%">
        <div
          ref="mapDiv"
          style="width: 100%; height: 800px"
          class="rounded-3"
        />
      </div>
    </div>
  </div>
</template>

<script>
/*eslint-disable no-undef*/ 
/*eslint no-empty: "error"*/
import { ref, onMounted, onUnmounted, watch } from "vue";
import useGeolocation from "./useGeolocation";
import { useRandom } from "./useRandom";
import { Loader } from "@googlemaps/js-api-loader";
import Haversine from "./haversine";
import { faCar, faFlagCheckered } from "@fortawesome/free-solid-svg-icons";
import { useNewPosition } from "./useNewPosition";

var SlidingMarker;

const GOOGLE_MAPS_APY_KEY = 'AIzaSyA4VGWfeMu2iduhK2rHSqoeXXaPFWJlulA';

export default {
  name: "App",

  setup() {    

    let metaPositionCoords = ref({
      lat: 1.2215372,
      lng: -77.2934816,
    });

    let rangeCarGenerator = null;
    let metaMarker = null;
    let intervalRace = null;
    let bots = ref([]);
    let botsGenerated = ref(false);
    let raceStarted = ref(false);
    let infoWindow = ref(null);
    let finishedRace = ref(false);

    const loader = new Loader({ apiKey: GOOGLE_MAPS_APY_KEY });

    const mapDiv = ref(null);

    let map = ref(null);
    let clickListener = null;
    onMounted(async () => {
      try{
        const res = await useGeolocation()
        metaPositionCoords.value.lat = res.coords.latitude;
        metaPositionCoords.value.lng = res.coords.longitude;
      }catch(e){
        //empty
      }
      
      await loader.load();
      SlidingMarker = require("marker-animate-unobtrusive");
      map.value = new google.maps.Map(mapDiv.value, {
        center: metaPositionCoords.value,
        zoom: 14,
      });

      

      clickListener = map.value.addListener(
        "click",
        ({ latLng: { lat, lng } }) =>
          (metaPositionCoords.value = { lat: lat(), lng: lng() })
      );

      metaMarker = new google.maps.Marker({
        position: metaPositionCoords.value,
        map: map.value,
        icon: {
          path: faFlagCheckered.icon[4],
          fillColor: "#000000",
          fillOpacity: 1,
          anchor: new google.maps.Point(
            faFlagCheckered.icon[0] / 4, // width
            faFlagCheckered.icon[1] / 2 // height
          ),
          strokeWeight: 0.2,
          strokeColor: "#000",
          scale: 0.075,
        },
      });

      rangeCarGenerator = new google.maps.Circle({
        strokeColor: "#FF000077",
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillOpacity: 0.2,
        map: map.value,
        center: metaPositionCoords.value,
        radius: 3000,
        editable: true,
      });

      infoWindow.value = new google.maps.InfoWindow({
        content: "",
      });
    });

    onUnmounted(async () => {
      if (clickListener) clickListener.remove;
    });

    watch([metaPositionCoords], () => {
      metaMarker.setPosition(metaPositionCoords.value);
      rangeCarGenerator.setCenter(metaPositionCoords.value);
      map.value.setCenter(metaPositionCoords.value);
      bots.value.forEach((bot) => {
        bot.setMap(null);
        bot.setVisible(false);
      });
      bots.value.splice(0, bots.value.length);
      botsGenerated.value = false;
    });

    function generateRandomPosition() {
      let position = null;

      const limits = rangeCarGenerator.getBounds();
      const north = limits.getNorthEast().lng();
      const east = limits.getNorthEast().lat();
      const south = limits.getSouthWest().lng();
      const west = limits.getSouthWest().lat();

      while (!position) {
        const tempPointlng = Math.random() * (north - south) + south;
        const tempPointlat = Math.random() * (east - west) + west;
        const tempPoint = { lat: tempPointlat, lng: tempPointlng };
        const rangeDistance = Haversine(metaPositionCoords.value, tempPoint);

        if (
          rangeDistance > 1800 &&
          rangeDistance < rangeCarGenerator.getRadius()
        ) {
          position = tempPoint;
        }
      }

      return position;
    }

    function generateBots() {
      finishedRace.value = false;
      stopRace();
      botsGenerated.value = true; //Se define la generacion de los bots

      if (bots.value) {
        bots.value.forEach((bot) => {
          bot.setMap(null);
          bot.setVisible(false);
        });
        bots.value.splice(0, bots.value.length);
      }

      useRandom().forEach((car) => {
        addBot(car);
      });

      topPositions();
    }

    function addBot(car) {
      botsGenerated.value = true; //Se define la generacion de los bots
      let randomP = generateRandomPosition();

      bots.value.push(
        new SlidingMarker({
          position: randomP,
          map: map.value,
          distance: Haversine(randomP, metaPositionCoords.value),
          battery: 100,
          batteryRechargeTime: 6,
          title: `${car.title} #0${car.id}`,
        })
      );
      topPositions();
    }

    function moveBots() {
      raceStarted.value = true; // se define el inicio de la carrera
      finishedRace.value = false;
      let carsFinishedRace = (car) => car.distance === 0;
      intervalRace = setInterval(() => {
        bots.value.forEach((car) => {
          const { defPosition, newDistance, newPosition } = moveBot(car);
          if (car.distance === 0) return;
          let batteryC = Math.floor(Math.random() * (30 - 10 + 1) + 10);

          if (batteryC > car.battery) {
            // verificar que la bateria quede en cero
            batteryC = car.battery;
          }

          car.battery -= batteryC; // restar el aleatorio del consumo de la bateria

          if (car.battery > 0) {
            car.setPosition(defPosition); // comprobar si el carro tiene bateria

            car.distance = newDistance;

            //Comprobar si la distancia es menor a la minima distancia aleatoria
            if (car.distance < newPosition.randomTemp) {
              car.setPosition(metaPositionCoords.value);
              return;
            }
          }

          if (car.battery === 0 && car.batteryRechargeTime > 0) {
            car.batteryRechargeTime -= 1;
            return;
          } else if (car.battery === 0 && car.batteryRechargeTime === 0) {
            car.battery = 100;
            car.batteryRechargeTime = 6;
          }
        });

        topPositions();

        if (bots.value.every(carsFinishedRace)) {
          window.clearInterval(intervalRace);
          finishedRace.value = true;
        }
      }, 1000);
    }

    function moveBot(car) {
      const actPosition = {
        lat: car.getPosition().lat(),
        lng: car.getPosition().lng(),
      };
      const newPosition = useNewPosition(
        actPosition,
        metaPositionCoords.value,
        Haversine(actPosition, metaPositionCoords.value)
      );
      const defPosition = {
        lat: car.getPosition().lat() + newPosition.lat,
        lng: car.getPosition().lng() + newPosition.lng,
      };
      const newDistance = Haversine(actPosition, metaPositionCoords.value);

      return { defPosition, newDistance, newPosition };
    }

    function stopRace() {
      if (raceStarted.value) raceStarted.value = false;
      window.clearInterval(intervalRace);
    }

    function addCar() {
      addBot(useRandom()[1]);
    }

    function infoCar(car) {
      infoWindow.value.setContent(car.title);
      infoWindow.value.open(map.value, car);
    }

    function topPositions() {
      bots.value.sort((a, b) => {
        if (a.distance < b.distance) return -1;
        else if (a.distance > b.distance) return 1;
        return 0;
      });

      // CHANGE CAR COLOR (MARKER)
      bots.value.forEach((car, i) => {
        if (i === 0) {
          car.setIcon({
            path: faCar.icon[4],
            fillColor: "#008F39",
            fillOpacity: 1,
            anchor: new google.maps.Point(
              faCar.icon[0] / 2, // width
              faCar.icon[1] // height
            ),
            strokeWeight: 1,
            strokeColor: "#000",
            scale: 0.05,
          });
        } else if (i === bots.value.length - 1) {
          car.setIcon({
            path: faCar.icon[4],
            fillColor: "#C81D11",
            fillOpacity: 1,
            anchor: new google.maps.Point(
              faCar.icon[0] / 2, // width
              faCar.icon[1] // height
            ),
            strokeWeight: 1,
            strokeColor: "#000",
            scale: 0.05,
          });
        } else
          car.setIcon({
            path: faCar.icon[4],
            fillColor: "#E5BE01",
            fillOpacity: 1,
            anchor: new google.maps.Point(
              faCar.icon[0] / 2, // width
              faCar.icon[1] // height
            ),
            strokeWeight: 1,
            strokeColor: "#000",
            scale: 0.05,
          });
      });
    }

    return {
      metaPositionCoords,
      mapDiv,
      generateBots,
      moveBots,
      stopRace,
      addCar,
      bots,
      botsGenerated,
      raceStarted,
      infoCar,
      finishedRace,
    };
  },
};
</script>

<style>
#app {
  display: flex;
  justify-content: center;
}
</style>
