export function useRandom() {

    function random(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    }
  
    const randomCars = random(5, 10);
    const arrayCars = [];
  
    const nameCars = [
      "Renault Twingo",
      "Chevrolet Spark",
      "Toyota Land Cruiser",
      "Renault 9",
      "Chevrolet Chevette",
      "Renault 12",
      "Chevrolet Sprint",
      "Jeep Willys",
      "Mazda 323",
      "Renault 4",
    ];
  
    for (let i = 0; i < randomCars; i++) {
      arrayCars.push({
        id: i,
        title: nameCars[random(0,9)],
      });
    }
  
    return arrayCars;
  }