


export function useNewPosition(origen, destine, distance) {
    const randomTemp = randomAdvance(50,100)
    const lat = ((destine.lat - origen.lat) * randomTemp) / distance
    const lng = ((destine.lng - origen.lng) * randomTemp) / distance
    return {lat, lng, randomTemp}
}

function randomAdvance(min, max){
    return Math.floor(Math.random() * (max - min + 1) + min);
}

